.listItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  margin: 10px 0;
  padding: 0 10px;
  background: rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  border-radius: 8px;
  width: 300px;
  color: #03f46f;
}
